  }
}

.section-title {
  font-size: 26px;
  text-align: center;
  color: #333;
  margin: 0 0 30px;
  font-family: "GothamProMedium", sans-serif;
}

@media (min-width: 768px) {
  .section-title {
    font-size: 40px;
  }
}

.section-description {
  color: #666;
  line-height: 30px;
  text-align: center;
  font-size: 14px;
  margin: 0 0 30px;
