@media (min-width: 768px) {
  .section-description {
    font-size: 16px;
    margin: 0 0 60px;
  }
}

.fade {
  opacity: 0;
  -webkit-transition: opacity 0.15s linear;
  -o-transition: opacity 0.15s linear;
  transition: opacity 0.15s linear;
}

.fade.in {
  opacity: 1;
}

.modal-open {
  overflow: hidden;
}

.modal {
  display: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  -webkit-overflow-scrolling: touch;
  outline: 0;
}

.modal.fade .modal-dialog {
  transform: translate(0, -25%);
  transition: transform 0.3s ease-out;
}

.modal.in .modal-dialog {
  transform: translate(0, 0);
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
  color: #fff;
}

.modal-content {
  position: relative;
  background-color: #9ec01a;
  border-radius: 15px;
  outline: 0;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background-color: #000000;
}

.modal-backdrop.fade {
  opacity: 0;
  filter: alpha(opacity=0);
}

.modal-backdrop.in {
  opacity: 0.4;
  filter: alpha(opacity=40);
}

.modal-header {
  padding: 20px 20px 0;
  text-align: center;
  max-width: 420px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .modal-header {
    padding: 40px 15px 30px;
  }
}

.modal-header .close {
  width: 45px;
  height: 45px;
  border-radius: 45px;
  background: #ff5932;
  color: #fff;
  position: absolute;
  border: 0;
  font-size: 30px;
  outline: none;
  cursor: pointer;
  right: -5px;
  top: -5px;
}

@media (min-width: 768px) {
  .modal-header .close {
    right: -20px;
    top: -20px;
  }
}

.modal-header .modal-title {
  font-size: 28px;
  font-family: "GothamProMedium", sans-serif;
}

@media (min-width: 768px) {
  .modal-header .modal-title {
    font-size: 48px;
  }
}

.modal-header .modal-description {
  line-height: 24px;
  font-size: 14px;
}

@media (min-width: 768px) {
  .modal-header .modal-description {
    font-size: 16px;
  }
}

.modal-header .close {
  margin-top: -2px;
}

.modal-title {
  margin: 0;
  line-height: 1.42857143;
}

.modal-body {
  position: relative;
  padding: 15px 15px 40px;
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
}

.modal-body .modal-tel {
  padding-left: 45px;
  position: relative;
}

.modal-body .modal-tel b {
  font-family: "GothamProBold", sans-serif;
  font-size: 20px;
}

.modal-body .modal-tel .icon {
  position: absolute;
  left: 0;
  top: 0;
}

.modal-body .form-control {
  margin: 0 0 20px;
}

.modal-body .form-control input[type="text"],
.modal-body .form-control input[type="tel"] {
  width: 100%;
  background: transparent;
  border: solid 2px #d1e193;
  width: 100%;
  height: 46px;
  color: #fff;
  padding: 0 0 0 20px;
  outline: none;
  border-radius: 40px;
  font-family: "GothamProMedium", sans-serif;
}

.modal-body input[type="submit"] {
  width: 203px;
  height: 45px;
  border: none;
  background: #ff5932;
  -webkit-border-radius: 40px;
  border-radius: 40px;
  outline: 0;
  color: #fff;
  text-transform: uppercase;
  font-family: GothamProBold,sans-serif;
  -webkit-transition: all .3s linear 0s;
  transition: all .3s linear 0s;
  margin-top: 20px;
  cursor: pointer;
  -webkit-box-shadow: 0 7px 40px 18px rgba(93, 115, 7, 0.24);
  box-shadow: 0 7px 40px 18px rgba(93, 115, 7, 0.24);
}

@media (min-width: 768px) {
  .modal-body input[type="submit"] {
    margin-top: 40px;
  }
}

.modal-body input[type="submit"]:hover {
  background: #e2411b;
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

@media (min-width: 768px) {
  .modal-dialog {
    width: 620px;
    margin: 60px auto;
  }
